import { React, useState, useRef } from 'react';
import { ReviewFormModal } from './ReviewFormModal/ReviewFormModal';
import Carousel from 'react-elastic-carousel';
import slide1 from '../../assets/img/user1.png';
import slide2 from '../../assets/img/user2.png';
import slide3 from '../../assets/img/user3.png';
import slide4 from '../../assets/img/user4.png';
import left from '../../assets/img/left.png';
import right from '../../assets/img/right.svg';
import plus from '../../assets/img/plus.svg';
import s from './Reviews.module.scss';
import Card from './Card/Card';
import Modal from 'react-modal';

Modal.setAppElement('#root');

const slides = [
    {
        id: 1,
        imgUrl: slide1,
        userName: ' ',
        date: '08.01.2022',
        text: ' ,            .  KPI        -      ,    .  -   ,     ,   .    .',
    },
    {
        id: 2,
        imgUrl: slide2,
        userName: ' ',
        date: '08.01.2022',
        text: '   ,   ,      .     ,      ,    .      .         .',
    },
    {
        id: 3,
        imgUrl: slide3,
        userName: ' ',
        date: '26.12.2021',
        text: '    ilink  .      :    ,   ,   .        ,    ,  ,   .     .  ,      ilink.',
    },
    {
        id: 4,
        imgUrl: slide4,
        userName: ' ',
        date: '16.12.2021',
        text: '       ,      HR-    .       HR-.   .  ,   .    .   ,   ,    .     .  , .',
    },
];



const Reviews = () => {
    const [isOpenFormModal, setIsOpenFormModal] = useState(false);

    const sliderRef = useRef(null);
   
    const breakPoints = [
        { width: 1, itemsToShow: 1 },
        { width: 576, itemsToShow: 2 },
    ];

    return (
        <div className={s.container}>
            <div className={s.container__wrapper}>
                <div className={s.reviews}>
                    <div className={s.reviews__top}>
                        <h2></h2>
                        <button
                            className={s.reviews__top__buttonReview}
                            onClick={() => setIsOpenFormModal(true)}
                        >
                            <img src={plus} />+  
                        </button>
                    </div>
                    <div></div>
                </div>

                <div className={s.slider}>
                    <Carousel
                        breakPoints={breakPoints}
                        ref={sliderRef}
                        outerSpacing={-24}
                        showArrows={false}
                        disableArrowsOnEnd={true}
                    >
                        {slides.map((slide, index) => (
                            <div className={s.slider__card}>
                                <Card
                                    key={index}
                                    imgUrl={slide.imgUrl}
                                    userName={slide.userName}
                                    date={slide.date}
                                    text={slide.text}
                                />
                            </div>
                        ))}
                    </Carousel>
                </div>
            </div>
            <div className={s.container__buttonsWrapper}>
                <button
                    className={s.buttons}
                    onClick={() => sliderRef.current.slidePrev()}
                >
                    <img src={left} />
                </button>
                <button
                    className={s.buttons}
                    onClick={() => sliderRef.current.slideNext()}
                >
                    <img src={right} />
                </button>
            </div>

            <ReviewFormModal
                isOpenFormModal={isOpenFormModal}
                setIsOpenFormModal={() => setIsOpenFormModal(false)}
            />
        </div>
    );
};

export default Reviews;
